.leftMenu{
    z-index: 1;
    width: 280px;
    height: 100%;
    min-height: 100vh;
    background: rgba(34, 34, 34, 0.6);
    /* 배경에 흐림 필터추가 */
    backdrop-filter: blur(10px);
    padding: 30px 15px;
    /* 축소되면 아이콘만 보이게 할거임 */
    overflow: hidden;
    /* cubic-bezier() 효과 */
    transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    border-right: 1px solid #2c2b2b;
}

.leftMenu .logoContainer{
    color: #f1f1f1;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.leftMenu .logoContainer i{
    font-size: 40px;
}

.leftMenu .logoContainer i:nth-of-type(2){
    font-size: 20px;
}

.leftMenu .logoContainer h2{
    font-size: 25px;
}

.searchBox{
    width: 100%;
    height: 45px;
    position: relative;
    margin: 30px 0;
    /* margin-top: 20px; */
    color: #848484;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0px 0px 20px rgba(0,0,0, 0.4);
}

.searchBox input{
    width: 100%;
    height: 100%;
    outline: none;
    border: none;
    background-color: rgba(0,0,0, 0.5);
    padding-left: 45px;
    color: #f1f1f1;
    font-size: 14px;
    font-weight: bold;
}

.searchIcon{
    position: absolute;
    top: 0;
    left: 0;
    color: #848484;
    width: 45px;
    height: 45px;
    text-align: center;
    /* line-height로 수직잡기 */
    line-height: 50px;
    font-size: 20px;
}

.MenuContainer{
    margin: 20px 0px 30px;
}

/* 다른 클래스를 같이 적용 , 쉼표 사용 */
.MenuContainer .title{
    color: #848484;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
    text-transform: uppercase;
}

.MenuContainer ul{
    width: 100%;
    padding: 0;
}

.MenuContainer ul li{
    list-style: none;
    margin: 10px 0px;
    width: 100%;
    padding: 5px 0px;
    position: relative;
}

.MenuContainer ul li a{
    text-decoration: none;
    color: #848484;
    display: flex;
    align-items: center;
    font-size: 20px;
}

.MenuContainer ul li a i{
    margin-right: 15px;
    font-size: 20px;
}

.MenuContainer ul li::before{
    position: absolute;
    content: "";
    top: 0;
    left: -15px;
    width: 0px;
    height: 100%;
    background-color: #49e12e;
    border-radius: 5px;
    
}

/* before 태그 호버하는법 */
/* 클릭했을때 useEffet .active를 활용하여 남아있게함 */
.MenuContainer ul li:hover::before,
.MenuContainer ul li.active::before{
    width: 5px;
    transition: 0.3s;
}

.MenuContainer ul li:hover a,
.MenuContainer ul li.active a{
    color: #f1f1f1;
    transition: 0.3s;
}

.playListContainer{
    cursor: pointer;
    margin: 25px 0px;
}

.playListContainer .nameContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #848484;
    margin-bottom: 10px;
}

.playListContainer .nameContainer p{
    font-size: 18px;
    text-transform: uppercase;
    font-weight: bold;

}

.playListContainer .nameContainer i{
    cursor: pointer;
    color: #f1f1f1;
}

.playListContainer .playListScroll{
    height: 150px;
    overflow-x: hidden;
    overflow-y: scroll;
    position: relative;
}

.playListContainer .playListScroll .playList{
    display: flex;
    align-items: center;
    color: #848484;
    margin: 10px 0px;

}

.playListContainer .playListScroll .playList:hover{
    color: #f1f1f1;
    transition: 0.3s;

}

.playListContainer .playListScroll .playList i{
    font-size: 20px;
}

.playListContainer .playListScroll .playList .list{
    margin-right: 15px;
}

.playListContainer .playListScroll .playList .trash{
    /* 화면 오른쪽에 유지 */
    margin-left: auto;
}

.playListContainer .playListScroll .playList p{
    font-size: 18px;
    /* 텍스트가 넘칠경우....으로 생략 */
    text-overflow: ellipsis;
    overflow: hidden;
    display: inline;
}

.trackList{
    border-top: 2px solid rgba(255, 255, 255, 0.1);
    padding-top: 30px;
}

.trackList .top{
    display: flex;
    align-items: center;
}

.trackList .top img{
    width: 55px
}

.trackList .top .trackName{
    margin-left: 10px;
    color: #f1f1f1;
    font-size: 17px;
}

.trackList .top .trackName .trackSpan{
    display: block;
    font-size: 16px;
    color: #848484;
}

.trackList .bottom{
    margin-top: 26px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #848484;
}

.trackList .bottom i{
    font-size: 22px;
}

.trackList .bottom i:hover{
    color: #f1f1f1;
    transition: 0.3s;
    cursor: pointer;
}

.trackList .bottom input{
    position: relative;
    height: 7px;
    border: none;
    outline: none;
    border-radius: 5px;
    background: rgba(255, 255, 255, 0.1);
    /* 네이티브로 지원되는 모양들을 해제하거나 추가할때 이 속성을 이용할 수 있다. */
    appearance: none;
}

/*** Chrome & Safari 슬라이더 스타일***/
/* 배경 슬라이더 */
.trackList .bottom input::before{
    /* 인풋박스에 absolute */
    position: absolute;
    content: " ";
    top: 0;
    left: 0;
    background: #49e12e;
    width: 50%;
    height: 100%;
    border-radius: 10px;
    z-index: 2;
    /* ? */
    transition: width 250ms linear;
}

/* 엄지슬라이더 수정 */
.trackList .bottom input::-webkit-slider-thumb{
    /* 기본적으로 보이는걸 없앰 */
    -webkit-appearance: none;

    width: 10px;
    height: 22px;
    border-radius: 10px;
    background: #f1f1f1;
    border: 2px solid black;
    cursor: pointer;
    position: relative;
    margin: -2px 0 0 0;
    z-index: 3;
    box-sizing: border-box;
    transition: all 250ms linear;

}

/*** firefox 슬라이더 스타일***/
/* 배경 슬라이더 */
.trackList .bottom input::-moz-progress-bar{
    background: #49e12e;
    width: 50%;
    height: 100%;
    border-radius: 10px;
    z-index: 2;
    /* ? */
    transition: width 250ms linear;
}

/* 엄지 슬라이더 */
.trackList .bottom input::-moz-range-thumb{
    background: #49e12e;
    width: 50%;
    height: 100%;
    border-radius: 10px;
    z-index: 2;
    /* ? */
    transition: width 250ms linear;
}

/* 모바일 반응형으로 만들기 */

@media screen and (max-width : 550px){
    /* 550px 이하면 왼쪽 메뉴가 줄어듬 */
    .leftMenu{
        width: 64px;
        min-width: 64px;
    }

    .leftMenu .searchBox{
        display: none;
    }

    .leftMenu .trackList{
        display: none;
    }

    .leftMenu .playListContainer{
        display: none;
    }

    .leftMenu .MenuContainer .title{
        font-size: 12px;
        
    }
    .leftMenu .MenuContainer ul li a span{
        display: none;
    }

    .leftMenu .MenuContainer ul li a i{
        /* 메뉴의 아이콘 사이즈를 좀더 키워줌 */
        font-size: 26px;
    }

    .leftMenu .MenuContainer ul li{
        margin: 25px 0;
    }

    .leftMenu .logoContainer h2{
        margin-left: 15px;
    }
}