.musicPlayer{
    width: 100%;
    padding: 10px;
    display: flex;
}

.songImage{
    width: 140px;
    min-width: 140px;
    height: 120px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
}

.songImage img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.songAttributes{
    width: 100%;
    padding-left: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.songAttributes .top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.songAttributes .top .middle{
    width: 80%;
    margin-left: -75px;
}


.songAttributes .top .left,
.songAttributes .top .right{
    color: #595959;
    /* font-size:  16px; */
    cursor: pointer;
    display: flex;
    align-items: center;
}

.songAttributes .top .left i,
.songAttributes .top .right i{
    margin: 0px 15px;
    font-size: 26px;
}

.songAttributes .top .left .loved i{
    color: #49e12e;
    filter: drop-shadow(0px 0px 4px #49e12e);
}

.rhap_container{
    background-color:transparent !important;
    box-shadow: none !important;
}

.rhap_container svg{
    color: #49e12e;
}

.rhap_time{
    color: #f1f1f1 !important;
}





