.mainContainer{
    height: 100vh;
    z-index: 1;
    /* 메인컨테이너가 빈공간을 메꾼다 flex-grow */
    flex-grow: 1;
    background: rgba(34, 34, 34, 0.6);
    backdrop-filter: blur(10px);
    transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, .1.275);
    overflow-y: scroll;
}

.banner{
    height: 350px;
    position: relative;
}

/* 배너 absolute로 왼쪽 위 붙음*/
.banner .bannerImg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: grayscale(100);
}

/* 배너밑에 올라오는 검은 그림자 효과 */
.banner .bottomLayer{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-image: linear-gradient(transparent, rgba(0,0,0,0.9));
}

/* 배너의 전체 사각형 */
.banner .content{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 20px 30px;
    display: flex;
    flex-direction: column;
    z-index: 3;
}

.banner .content .breadCrump{
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.banner .content .breadCrump i{
    color: #f1f1f1;
    font-size: 22px;
    cursor: pointer;
}

.banner .content .breadCrump p{
    color: #848484;
    font-size: 22px;
    font-weight: 600;
    letter-spacing: 1px;
}

.banner .content .breadCrump p span{
    color: #f1f1f1;
}


.banner .content .artist{
    margin-top: auto;
    color: #f1f1f1;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.banner .content .artist .left .name{
    display: flex;
    align-items: center;
}

.banner .content .artist .left .name h2{
    font-size: 50px;
}

.banner .content .artist .left .name i{
    font-size: 34px;
    margin-top: 15px;
    margin-left: 15px;
}

.banner .content .artist .left p{
    font-size: 16px;
}

.banner .content .artist .left p i{
    margin-right: 6px;
}

.banner .content .artist .left p span{
    color: #848484;
    margin-left: 5px;
}

.banner .content .artist .right{
    display: flex;
    align-items: center;
}

.banner .content .artist .right a{
    text-decoration: none;
    display: flex;
    align-items: center;
    padding: 13px 33px;
    font-size: 16px;
    color: #f1f1f1;
    margin: 0px 15px;
    /* 버튼 border-radius */
    border-radius: 100vh;
    /* background: #555; */
    font-weight: bold;
    text-align: center;
}

.banner .content .artist .right a i{
    margin-right: 10px;
    font-size: 12px;
}

.banner .content .artist .right a:nth-child(1){
    background: #39aa24;
    margin-left: 0px;
}

.banner .content .artist .right a:nth-child(2){
    background: rgba(0,0,0,0.3);
    margin-right: 0;
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.banner .content .artist .right a:hover{
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
}


.menuList{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 22px 35px;
    background: #2c2c2c;
}

.menuList ul{
    display: flex;
    align-items: center;
}

.menuList ul li{
    list-style: none;
    margin: 0px 30px;
    position: relative;
}

.menuList ul li a{
    text-decoration: none;
    color: #848484;
    font-size: 16px;
    font-weight: bold;
}

.menuList ul li::before{
    position: absolute;
    content: "";
    bottom: -18px;
    left: 5px;
    width: 0px;
    height: 5px;
    border-radius: 5px;
    background: #f1f1f1;
}

/* 밑에 바 */
.menuList ul li:hover::before, 
.menuList ul li.active::before{
    width: 80%;
    transition: width 0.3s cubic-bezier(0.175, 0.885, 0.32,1.275);
}

/* a태그 글씨 자체 */
.menuList ul li:hover a, 
.menuList ul li.active a{
    color: #f1f1f1;
    transition: 0.3s;
}

.menuList p{
    color: #f1f1f1;
    font-size: 16px;
}

.menuList p i{
    margin-right: 6px;
    font-size: 16px;
}

.menuList p span{
    /* display: block; */
    font-size: 14px;
    text-align: center;
    color: #848484;
    margin-left: 10px;
}

/* 메인 오디오 리스트 스타일 */

.audioList{
    padding: 20px 35px;
}

.audioList .title{
    font-size: 22px;
    color: #f1f1f1;
}

.audioList .title span{
    color: #848484;
    font-size: 14px;
    margin-left: 15px;
}

.songsContainer{
    height: 460px;
    overflow-y: scroll;
    overflow-x: hidden;
}

.songs{
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: 600;
    color: #848484;
    width: 100%;
    margin: 10px 0;
    padding: 5px;
}

.songs:hover, .songs.active{
    color: #f1f1f1;
    transition: 0.3s;
}

.count{
    font-size: 20px;
    margin-right: 15px;
}

.song{
    display: flex;
    align-items: center;
    width: 100%;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding-bottom: 5px;
    cursor: pointer;
}

.song .imgBox{
    width: 50px;
    height: 50px;
    min-width: 50px;
    border-radius: 0.5em;
    background: #555;
    margin-right: 6px;
    overflow: hidden;
}

.song .imgBox img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.song .section{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.song .section .songName{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 50%;
}

.song .section .hits{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.song .section .hits .hit,
.song .section .hits .duration{
    margin-right: 15px;
}

.song .section .hits .hit i,
.song .section .hits .duration i{
    font-size: 14px;
    margin-right: 5px;
}


.song .section .hits .favourite i{
    color: #49e12e;
    /* 뒤에 생기는 그림자 */
    filter: drop-shadow(0px 0px 4px #49e12e);
}


/* 모바일 반응형 */

@media screen and (max-width: 550px){
    .mainContainer{
        min-width: 380px;
    }

    .content{
        padding: 10px 15px;
    }

    .banner .content .artist .left .name h2{
        font-size: 25px;
    }

    .banner .content .artist .right a{
        padding: 5px 15px;
        font-size: 12px;
        margin: 0 10px;
    }

    .menuList{
        padding: 5px 10px;
    }

    .menuList ul li{
        margin: 0 5px;
    }

    .menuList p{
        font-size: 12px;
    }

    .menuList p i{
        font-size: 10px;
    }

    .audioList{
        padding: 10px 5px;
    }
}