.App{

  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  display: flex;
}

.background{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url(./img/background2.jpg) no-repeat center;
  background-size: cover;
  filter: grayscale(100);
  /* z-index로 배경을 뒤로 빼기 */
  z-index: -1;
}